import React from "react";

export default function Destroy() {
  function handleDestroy(e) {
    const main = document.getElementById("main");
    const contactBtn = document.querySelector("a.linkButton");
    main.removeChild(contactBtn);
    const scrollBar = document.querySelector(".scroll-bar");
    scrollBar.style.cssText = "width:0;padding:0";
    const mainHtml = main.innerHTML.replace(/\n+/g, "");
    let newHtml = "";
    let isText = false;
    for (let i = 0; i < mainHtml.length; i++) {
      let letter = mainHtml[i];
      if (letter === "<") isText = false;
      if (isText && letter.match(/[^\s\n]+/)) {
        letter = `<span class="destroyed">${letter}</span>`;
      }
      newHtml += letter;
      if (letter === ">") isText = true;
    }
    main.innerHTML = newHtml;
    document.body.classList.add("chaos");
    const destroyed = document.querySelectorAll(
      ".destroyed, .socials button, .socials a"
    );
    let destruction = {};
    for (let i = 0; i < destroyed.length; i++) {
      destruction[i] = {
        dX: Math.random() <= 0.49 ? 1 : -1,
        dY: Math.random() <= 0.49 ? 1 : -1,
      };
      const randY = Math.floor(window.innerHeight * Math.random());
      const randX = Math.floor(window.innerWidth * Math.random());
      const animationDelay = Math.floor(1000 * Math.random());
      destroyed[
        i
      ].style.cssText = `top:${randY}px;left:${randX}px;animation-delay:${animationDelay}ms`;
    }

    function chaos() {
      const W = window.innerWidth;
      const H = window.innerHeight;

      for (let i = 0; i < destroyed.length; i++) {
        let top = parseInt(destroyed[i].style.top) || 0;
        let left = parseInt(destroyed[i].style.left) || 0;

        let dirX = 100 * destruction[i].dX;
        let dirY = 100 * destruction[i].dY;

        if (top + dirY > H) {
          top = H;
          destruction[i].dY *= -1;
        } else if (top + dirY < 0) {
          top = 0;
          destruction[i].dY = 1;
        } else {
          top += dirY;
        }
        if (left + dirX > W) {
          left = W;
          destruction[i].dX *= -1;
        } else if (left + dirX < 0) {
          left = 0;
          destruction[i].dX = 1;
        } else {
          left += dirX;
        }

        destroyed[i].style.top = top + "px";
        destroyed[i].style.left = left + "px";
        destroyed[i].style.zIndex = Math.floor(
          destroyed.length * Math.random()
        );
      }
    }
    chaos();
    setInterval(chaos, 1000);
    const destroyer = document.getElementById("forbidden-button");
    destroyer.remove();
    document.title = "Chaos! 😱";
  }
  return (
    <button id="forbidden-button" onClick={handleDestroy}>
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="16"
        height="16"
        fill="currentColor"
        viewBox="0 0 16 16"
      >
        <path d="M7.938 2.016A.13.13 0 0 1 8.002 2a.13.13 0 0 1 .063.016.15.15 0 0 1 .054.057l6.857 11.667c.036.06.035.124.002.183a.2.2 0 0 1-.054.06.1.1 0 0 1-.066.017H1.146a.1.1 0 0 1-.066-.017.2.2 0 0 1-.054-.06.18.18 0 0 1 .002-.183L7.884 2.073a.15.15 0 0 1 .054-.057m1.044-.45a1.13 1.13 0 0 0-1.96 0L.165 13.233c-.457.778.091 1.767.98 1.767h13.713c.889 0 1.438-.99.98-1.767z" />
        <path d="M7.002 12a1 1 0 1 1 2 0 1 1 0 0 1-2 0M7.1 5.995a.905.905 0 1 1 1.8 0l-.35 3.507a.552.552 0 0 1-1.1 0z" />
      </svg>
      <span>Do not click!</span>
    </button>
  );
}
